<template>
  <div class="main-content">
    <el-tabs 
      v-model="editableTabsValue" 
      type="card" 
      @tab-click="changeEditableTabsValue" 
      @tab-remove="removeTab" >
      <el-tab-pane
        v-for="item in editableTabs"
        :key="item.name"
        :label="item.label"
        :name="item.name"
        :closable="item.close"
      >
        <keep-alive>
          <component :is="item.name" />
        </keep-alive>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import myMain from '@/page/Main/main';
import Student from '@/page/Main/student';
import Teacher from '@/page/Main/teacher';
import Major from '@/page/Main/major';
import professionalTask from '@/page/Main/professionalTask';
import Grade from '@/page/Main/grade';
import Department from '@/page/Main/department';
import Role from '@/page/Main/role';
import Message from '@/page/Main/message';
import PersonMsg from '@/page/Main/personMsg';
import ChooseDirection from '@/page/Main/chooseDirection';

import axios from 'axios';
import { mapState, mapMutations, mapActions } from "vuex";
export default {
    components: {
      myMain,
      Student,
      Teacher,
      Major,
      professionalTask,
      Grade,
      Department,
      Role,
      Message,
      PersonMsg,
      ChooseDirection,
    },
  data() {
    return {};
  },
  computed: {
    ...mapState('tabs', ["editableTabs"]),
    editableTabsValue: {
        get() {
            return this.$store.state.tabs.editableTabsValue;
        },
        set(val) {}
    },
  },
  methods: {
    ...mapMutations('tabs', ["changeEditableTabsValue", "setTab", "removeTab"]),
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/main.scss";
</style>
